.introContainer{
  display:flex;
  justify-content: stretch;
  overflow: hidden;
  scroll-behavior: smooth;
}
.num {
  position: absolute;
  display: flex;
  color: #f5efdb;
  bottom: 32px;
  gap: 16px;
  top: 1054px;
  left: 50%;
  transform: translate(-50%, 0);

}
.num p:hover{
  cursor: pointer;
}
.introNav1, .introNav2, .introNav3{
  color: #f5efdb;
  font-family: "Cormorant Upright", serif;
  font-size: 24px;
  width: 45px;
  height: 45px;
  margin: 0;
  text-align: center;
}
.intro {
  width: 100vw;
  display: grid;
  grid-template-columns: 9.89vw 67.72vw 9.89vw;
  grid-template-rows: 736px 92px;
  color: white;
  padding: 80px 6.25vw;
  justify-content: center;
}
.hash {
  display: flex;
  grid-area: 1 / 1 / span 2 / span 1;
  align-items: center;
}
.hash p {
  color: #fff;
  font-size: 20px;

  writing-mode: vertical-rl;
  rotate: 180deg;
}

.scroll {
  color: #dcca87;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  grid-area: 1 / 3 / span 2 / span 1;
}
.linearScroll {
  height: 61px;
  width: 2px;
  background-image: linear-gradient(rgba(220, 202, 135, 1), rgba(220, 202, 135, 1), rgba(220, 202, 135, 0.24));
}
.content {
  display: flex;
  grid-area: 1 / 2 / span 1 / span 1;
  justify-content: space-between;
}

.para {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 35%;
  

}
.linearContent {
    margin: 0 0 24px 0;
  height: 2px;
  width: 30px;
  background-image: linear-gradient(to left, rgba(220, 202, 135, 1), rgba(220, 202, 135, 1), rgba(220, 202, 135, 0.24));
}
.para h5 {
  color: #fff;
  margin: 0 0 16px 0;

}
.para h1 {
  color: #dcca87;
  margin: 0 0 32px 0;

}
.para button{
    padding: 7px 30px;
}
.para p{
    margin: 0 0 32px 0;
    color: #AAA;
}

.image{
    display: flex;
    width: 50%;
    position: relative;
}
.image img{
    position: absolute;
    width: calc(100% - 7%);
    height: calc(100% - 7%);
    left: 3.5%;
    top: 3.5%;

}

.div1{
    width: 435px;
    height: 435px;
    background-color: #dcca87;
    align-self: flex-end;
}
.div2{
    width: 435px;
    height: 435px;
    background-color: #dcca87;
}