.vidcontainer {
  position: relative;
  margin: auto;
  width: 100%;
  height: 650px;

  overflow: hidden;
  clip-path: 0 0;
}
.vidcontainer video {
  position: absolute;
  top: -145%;
  bottom: -100%;
  left: -100%;
  right: -100%;

  margin: auto;

  min-width: 100%;
  min-height: 100%;
}
