.awardContainer {
  background: url("../assets/background2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 970px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
@keyframes scale {
  100% {
    transform: scale(1.01);
    opacity: 1;
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
.fadeTitle {
  position: relative;
  grid-row: 1 / span 1;
  grid-column: 2 / span 1;
}
.fadeDiv {
  position: relative;
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
  
  bottom: 36px;
}
.awardSig {
  position: absolute;
  color: #fafafa;
  font-family: Yellowtail;
  opacity: 0.85;
  font-size: 280px;
  right: 365px;
  top: 380px;
}
.iluimg {
  width: 480px;
  height: 690px;
}
.awardText {
  color: #ffffff;
  margin: 0 0 15px 0;
}
.awardContent h2 {
  color: #dcca87;
  margin: 0 0 15px 0;
}
.linear5 {
  height: 2px;
  width: 30px;
  background-image: linear-gradient(
    to right,
    rgba(220, 202, 135, 1),
    rgba(220, 202, 135, 1),
    rgba(220, 202, 135, 0.24)
  );
}
.awardContent {
    opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  width: 635px;
  margin-right: 180px;
}
.awardList {
  display: grid;
  grid-template: 1fr 1fr / 1fr 1fr;
}
.awardList p {
  color: #aaa;
  margin: 0;
}
.awardItem {
  display: grid;
  grid-template: 1fr 2fr / 1fr 3fr;
  align-items: center;
  height: 200px;
  width: 380px;
  column-gap: 30px;
}
.awardItem img {
  grid-row: 1 / span 2;
  grid-column: 1 / span 1;
  height: 105px;
}
