.navBar {
  padding: 45px 128px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;

}
.navBar img {
  width: 135px;
}
.nav {
  width: 400px;
  display: flex;
  justify-content: space-between;
}
a{
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  letter-spacing: 0.64px;
  text-decoration: none;
}
.nav p{
  cursor: pointer;
}
